"use strict";
const axios             = require("axios");
const countrySelect     = document.getElementById("select-country");
const provinceSelect    = document.getElementById("select-province");
const citySelect        = document.getElementById("select-district");

/**
 * Promise request
 * @param countryId
 */
const requestProvince = (countryId) =>{
    return axios({
        url: window.baseUrl+"admin/helper/ajax/province-list",
        method: "POST",
        headers:{
            "X-Requested-With": "XMLHttpRequest"
        },
        data:{
            countryId: countryId
        }
    });
};

/**
 * Append province data in province select
 * @param provinceList
 */
const appendProvinceData = (provinceList) =>{
    provinceList.forEach((province)=>{
        let optionAttribute = document.createElement("option");
        optionAttribute.text = province.province;
        optionAttribute.value = province.id;
        provinceSelect.append(optionAttribute);
    });
};

/**
 * DOM Listener
 */
if(!!countrySelect)
{
    countrySelect.addEventListener("change",() => {
        let countryId       = countrySelect.value;
        let requestResult   = requestProvince(countryId);
        requestResult.then((response)=>{
            appendProvinceData(response.data);
        }).catch((error)=>{
            throw new Error("Error: "+error);
        });
    });
}